h2 {
    margin: 0;
}

.newPackPanel {
    margin-top: 40px;
    margin-bottom: 60px;
    max-width: 65%;
    justify-content: space-between;
    display: flex;
    left: 18%;
    position: relative;
}

.newPackButton {
    width: 175px;
    height: 36px;
    border-radius: 30px;
    background-color: #366EFF;
    margin-top: 12px;
    border: none;
    color: white;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    cursor: pointer;
}
.paginationWrapper{
    max-width: 65%;
    justify-content: flex-start;
    display: flex;
    left: 18%;
    position: relative;
    margin: 40px 0;
}